:root {
  --inline-tag-height: 24px;
  --font-color: #4d4d4d;
  --border-color: #c3c2cf;
  --background-blue: rgba(102, 152, 255, 0.2);
  --background-white: white;
}

.emails-editor {
  overflow-x: hidden;
  overflow-y: auto;

  box-sizing: border-box;
  padding: 7px 4px 3px 6px;

  color: var(--font-color);
  background-color: #ffffff;
  border: 1px solid var(--border-color);
  cursor: text;

  line-height: var(--inline-tag-height);
}

.emails-editor__email {
  position: relative;

  display: inline-flex;
  height: var(--inline-tag-height);
  margin-right: 4px;
  margin-bottom: 3px;
  padding-left: 10px;
  padding-right: 7px;

  font-size: 14px;
  line-height: var(--inline-tag-height);

  border-radius: 12px;
  background-color: var(--background-blue);
  cursor: default;
}

.emails-editor__email--wrong {
  background-color: var(--background-white);
}

.emails-editor__email--wrong .emails-editor__email-content {
  border-bottom: 1px red solid;
}

.emails-editor__email-content {
  padding-right: 6px;
  text-overflow: ellipsis;
}

.emails-editor__email-delete {
  display: inline-block;
  height: var(--inline-tag-height);

  font-size: 17px;
  font-weight: lighter;

  cursor: pointer;
}
.emails-editor__email-delete::before {
  content: "\D7";
}

.emails-editor__input-container {
  position: relative;

  display: inline-flex;
  max-width: 100%;
  height: var(--inline-tag-height);
  margin-right: 4px;
  margin-bottom: 3px;
  padding-left: 10px;
  padding-right: 7px;

  vertical-align: top;

  font-size: 14px;
  line-height: var(--inline-tag-height);

  align-items: center;
}

.emails-editor__input-shadow {
  opacity: 0;

  font-size: inherit;
  line-height: inherit;
}

.emails-editor__input {
  position: absolute;
  left: 0;
  width: 100%;

  outline: none;
  height: 24px;
  display: inline-flex;
  width: 100%;
  resize: none;
  overflow: hidden;

  font-size: inherit;
  line-height: inherit;

  color: var(--font-color);
  border: none;
}
